.token-skeleton {
    width: 100%;
    min-height: calc(100vh - 66px);
    overflow: hidden auto;
    @include flex();
    align-items: stretch;
    &__left,
    &__right,
    &__main {
        transition: all ease-out 300ms;
        color: #fff;
    }
    &__left,
    &__right {
        width: 360px;
        flex-shrink: 0;
        flex-grow: 0;
        box-sizing: border-box;
        position: relative;
        &>button {
            display: inline-block;
            width: 18px;
            height: 38px;
            position: absolute;
            background: #1e242c;
            transition: rotate ease 100ms;
            z-index: 10;
        }
    }
    &__left {
        &>button {
            right: -18px;
            top: 100px;
            border-radius: 0 3px 3px 0
        }
        &.close {
            width: 0;
            opacity: 0.5;
            &>button {
                transform: rotate(180deg);
                border-radius: 3px 0 0 3px;
            }
        }
        &.open {
            width: 360px;
            opacity: 1;
            &>button {
                transform: rotate(0deg);
            }
        }
    }
    &__right {
        &>button {
            left: -18px;
            top: 100px;
            border-radius: 0 3px 3px 0;
            transform: rotate(180deg);
        }
        &.close {
            width: 0;
            opacity: 0.5;
            &>button {
                border-radius: 3px 0 0 3px;
                transform: rotate(0);
            }
        }
        &.open {
            width: 360px;
            opacity: 1;
            &>button {
                transform: rotate(180deg);
            }
        }
    }
    &__main {
        flex-shrink: 1;
        flex-grow: 1;
        padding: 0 15px;
    }
}
