@import '../../styles/share.scss';
.c-navTab {
    position: fixed;
    bottom: 0;
    min-height: 50px;
    width: 100%;
    background: #181d2399;
    //background: #fff;
    backdrop-filter: blur(5px);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
    z-index: 10;
    display: flex;
    padding: 8px 12px 12px;
    box-sizing: border-box;
    align-items: center;
    .left {
        // flex-grow: 1;
        // flex-shrink: 1;
        // max-width: 60%;
        width: 50%;
        display: flex;
        justify-items: center;
        justify-content: center;
        margin-right: 8px;
    }
    .right {
        width: 50%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        height: 100%;
        button {
            display: flex;
            align-items: center;
            margin-left: 16px;
        }
        .icon-btn {
            flex-direction: column;
            justify-content: center;
            span {
                font-size: 13px;
                margin-top: 4px;
            }
        }
    }
}

.div {
    width: 8px;
    flex-shrink: 0;
}

.trade-button {
    height: 40px;
    width: 100%;
    background: $upColor;
    color: #fff;
    font-size: 16px;
    border-radius: 3px;
    &.sell {
        background: $downColor;
    }
}

.switch-btn {
    position: relative;
    justify-content: space-between;
    min-width: 80px;
    height: 39px;
    border-radius: 19px;
    background: #232a32;
    -webkit-tap-highlight-color: transparent;
    outline: 0;
    span {
        position: relative;
        display: inline-block;
        width: 50%;
        z-index: 2;
        font-size: 13px;
        transition: all 0.2s;
    }
    &.USD {
        span {
            &:nth-of-type(1) {
                color: #fff;
            }
            &:nth-of-type(2) {
                color: #6d7986;
            }
        }
        &::before {
            transform: translateX(0);
        }
    }
    &.ETH {
        span {
            &:nth-of-type(1) {
                color: #6d7986;
            }
            &:nth-of-type(2) {
                color: #fff;
            }
        }
        &::before {
            transform: translate(100%, 0);
        }
    }
    &::before {
        content: "";
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        width: 50%;
        height: 39px;
        border-radius: 19px;
        transition: all .25s;
        background-color: #22d59f;
        transform: translate(0, 0);
    }
}