.c-input {
  &.showDropDown{
    .c-input-wrapper{
      z-index: 3;
    }

    .arrow{
      margin-left: 8px;
      transform: rotate(0deg) !important;
      transition: all .1s ease-in-out;
    }
  }

  &-title{
    font-size: 15px;
    margin-top: 10px;
    color: #ccc;
  }

  &-box{
    position: relative;
    height: 48px;
  }

  &-wrapper{
    width: 100%;
    display: flex;
    height: 100%;
    background: #181d23;
    border-radius: 3px;
    margin-top: 10px;
    padding: 8px;
    box-sizing: border-box;
    position: relative;

    .token-box{
      border-radius: 3px;
      flex-shrink: 0;
      flex-grow: 0;
      padding: 4px 8px;
      display: flex;

      span{
        display: block;
        line-height: 24px;
        margin-left: 8px;
      }

      &.select{
        background: #242a31;
        cursor: pointer;

        &:hover{
          background-color: #2d3641;
        }
      }

      .arrow{
        margin-left: 8px;
        transform: rotate(180deg);
        transition: all .1s ease-in-out;
      }
    }

    .input{
      flex-shrink: 1;
      flex-grow: 1;

      input{
        width: 100%;
        background: none;
        border: 0;
        text-align: right;
        font-size: 30px;
        color: #fff;

        &:focus{
          outline: 0;
        }
      }
    }
  }

  &-select{
    position: relative;
    z-index: 2;
    top: 0;
    width: 100%;
    background: #0e151dee;
    padding: 20px;
    box-sizing: border-box;
    box-shadow: 0 0 8px rgba(0, 0, 0, .5);
    backdrop-filter: blur(5px);

    .title{
      font-size: 16px;
    }

    .tokens{
      display: flex;
      margin-top: 7px;

      li{
        display: flex;
        padding: 6px 10px;
        background-color: #343d48;
        border-radius: 3px;
        text-decoration: none;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        margin: 10px 8px 0 0;
        align-items: center;
        cursor: pointer;
        color: #ccc;

        &:hover{
          color: #fff;
          background-color: #435265;
        }

        span {
          display: block;
          margin-left: 4px;
          font-size: 14px;
        }

        img{
          width: 24px;
          height: 24px;
        }
      }
    }

    input{
      width: 100%;
      box-sizing: border-box;
      height: 36px;
      margin-top: 20px;
      background: #181d23;
      border: 0;
      border-radius: 3px;
      padding: 0 10px;
      color: #fff;
      margin-bottom: 40px;

      &:focus{
        outline: 0;
        background: #343d48;
      }

      &:hover{
        background: #343d48;
      }
    }
  }

  &-btns{
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;

    button{
      height: 24px;
      color: #ccc;
      margin-left: 10px;
      border-radius: 3px;
      background: #242a31;
      padding: 0 8px;
      font-size: 13px;

      &:hover{
        background: #4f5a69;
        color: #fff;
      }

      &.active{
        background: #9488f0;
        cursor: default;
        color: #fff;
      }
    }
  }

  &-rate{
    text-align: right;
    margin-top: 8px;
    font-size: 13px;
    color: #ccc;

    i{
      font-style: normal;
      color: #6d7986;
    }
  }
}
