.c-languageManage{
  position: relative;
  cursor: pointer;

  .dropdown{
    min-width: 160px;
    background: #fff;
    position: absolute;
    top: 40px;
    z-index: 10;
    border: 2px solid #9387F0;
    border-radius: 10px;
    right: 0;
    padding: 10px 0;

    .item{
      font-size: 20px;
      line-height: 1.5;
      color: #999;
      padding: 6px 20px;
      cursor: pointer;
      display: flex;
      justify-content: space-between;
      align-items: center;

      img{
        display: flex;
        width: 18px;
      }


      &.active{
        color: #181D23;
        cursor: default;
      }
    }
  }

  span{
    display: flex;
    justify-content: center;
    align-items: center;


    .flag{
      border-radius: 3px;
      box-sizing: content-box;
      width: 18px;
    }

    .arrow{
      margin-left: 8px;
      width: 12px;
      vertical-align: middle;
    }
  }
}

.view2 .c-languageManage{
  .dropdown{
    min-width: 120px;
    top: 30px;
    padding: 0;

    .item{
      font-size: 14px;
      &:last-of-type{
        padding-bottom: 15px;
      }
      &:first-of-type{
        padding-top: 15px;
      }

      img{
        width: 16px;
      }

      &.active{

      }
    }
  }

  span{
    .arrow{
      width: 7px;
      margin-top: 2px;
      margin-left: 6px;
    }
  }
}
