.input-wrapper {
    width: 100%;
    height: 48px;
    transition: all ease 150ms;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    background-color: #4f5a69;
    padding-left: 8px;
    box-sizing: border-box;
    .input-container {
        flex: 1;
        display: flex;
        align-items: center;
        padding: 8px 0;
        height: 100%;
        box-sizing: border-box;
        .prefix {
            // width: 32px;
            // padding: 4px
            flex-grow: 0;
            flex-shrink: 0;
        }
        .container {
            flex: 1;
            height: 100%;
            input {
                display: inline-block;
                width: 100%;
                height: 100%;
                border: none;
                outline: 0;
                background-color: #4f5a69;
                color: #fff;
                padding: 0 6px;
                font-size: 14px;
                box-sizing: border-box;
                &::-webkit-input-placeholder {
                    color: #fff;
                }
            }
        }
    }
    .btn-search {
        background-color: #9488f0;
        button {
            min-width: 60px;
            max-width: 90px;
            height: 100%;
            color: #fff;
        }
    }
}