.c-icon-rotate{
  width: 600px;
  height: 600px;
  position: absolute;
  left: 12%;
  top: -10px;

  div{
    width: 100%;
    height: 100%;
    position: absolute;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }

  .bg1{
    background-image: url("./images/pan_bg.png");
    animation: 240s linear rotate infinite;
  }

  .line1{
    background-image: url("./images/pan_line1.png");
    animation: 6.1s linear rotate infinite;
  }

  .line2{
    background-image: url("./images/pan_line2.png");
    animation: 6.1s linear rotate2 infinite;
  }

  .line3{
    background-image: url("./images/pan_line3.png");
    animation: 8.1s linear rotate infinite;
  }

  .line4{
    background-image: url("./images/pan_line4.png");

    animation: 14.9s linear rotate2 infinite;
  }

  .line5{
    background-image: url("./images/pan_line5.png");
    animation: 28.1s linear rotate infinite;
  }

  .bg2{
    background-image: url("./images/pan_bg2.png");
  }

  .line6{
    background-image: url("./images/pan_line6.png");
    animation: 11s linear rotate infinite;
  }

  .items{
    width: 88px;
    height: 88px;
    position: absolute;
    animation: 18s linear rotate infinite;
    left: 50%;
    top: 50%;
    margin-left: -44px;
    margin-top: -44px;

    li{
      width: 100%;
      height: 100%;
      position: absolute;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      animation: 18s linear rotate2 infinite;

      &:nth-of-type(1){
        background-image: url("./images/pan_item1.png");
        top: -141px;
        left: 40px;
      }

      &:nth-of-type(2){
        background-image: url("./images/pan_item2.png");
        top: 210px;
        left: 150px;
      }

      &:nth-of-type(3){
        background-image: url("./images/pan_item3.png");
        top: 120px;
        left: -130px;
      }
    }
  }

  @keyframes rotate {
    from{
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  @keyframes rotate2 {
    from{
      transform: rotate(0deg);
    }
    to {
      transform: rotate(-360deg);
    }
  }

  @keyframes in {
    0%{
      transform: scale(0);
      opacity: 0;
    }
    50%{
      transform: scale(1.2);
      opacity: 1;
    }
    10% {
      transform: scale(1);
      opacity: 1;
    }
  }
}


$px: 210px;
@function px($n) {
  @return $n * $px / 600;
}

.view2 .c-icon-rotate{
  position: relative;
  //right: auto;
  width: px(600);
  height: px(600);
  margin-left: auto;
  margin-right: auto;
  left: auto;

  .items{
    width: px(88);
    height: px(88);
    margin-left: px(-44);
    margin-top: px(-44);

    li{

      &:nth-of-type(1){
        top: px(-141);
        left: px(40);
      }

      &:nth-of-type(2){
        top: px(210);
        left: px(150);
      }

      &:nth-of-type(3){
        top: px(120);
        left: px(-130);
      }
    }
  }
}

$px2: 480px;
@function px2($n) {
  @return $n * $px2 / 600;
}

.view1 .c-icon-rotate{
  position: relative;
  //right: auto;
  width: px2(600);
  height: px2(600);
  margin-left: auto;
  margin-right: auto;
  left: auto;

  .items{
    width: px2(88);
    height: px2(88);
    margin-left: px2(-44);
    margin-top: px2(-44);

    li{

      &:nth-of-type(1){
        top: px2(-141);
        left: px2(40);
      }

      &:nth-of-type(2){
        top: px2(210);
        left: px2(150);
      }

      &:nth-of-type(3){
        top: px2(120);
        left: px2(-130);
      }
    }
  }
}
