.maket-info {
    flex: 1;
    display: flex;
    align-items: center;
    color: #fff;
    margin-left: 20px;
    .item {
        display: flex;
        align-items: center;
        font-size: 13px;
        font-weight: 600;
        margin-right: 20px;
        &>div {
            margin-left: 12px;
            &>span {
                display: inline-block;
                width: 100%;
            }
            .bottomSpan {
                display: flex;
                align-items: center;
                margin-top: 4px;
                img {
                    margin-left: 5px;
                }
            }
            .eth {
                color: #545c66;
            }
            .to {
                color: #ff8a1f;
                padding-left: 8px;
            }
            .up {
                color: #46d7ab;
                ;
            }
            .down {
                color: #ff646d;
                ;
            }
            .amount {
                font-size: 18px;
            }
        }
    }
    .swtich-token {
        display: flex;
        align-items: center;
        height: 38px;
        font-weight: bold;
        font-size: 18px;
        margin: 0 10px;
        span {
            transition: all ease 100ms;
        }
        &.USD {
            span {
                &:nth-of-type(1) {
                    color: #46d7ab;
                    font-size: 22px;
                }
                &:nth-of-type(2) {
                    color: #545c66;
                }
            }
        }
        &.ETH {
            span {
                &:nth-of-type(1) {
                    color: #545c66;
                }
                &:nth-of-type(2) {
                    color: #46d7ab;
                    font-size: 22px;
                }
            }
        }
        img {
            margin: 0 6px;
        }
    }
}

@media screen and (max-width:960px) {
    .maket-info {
        margin-left: 0;
        // .item {
        //     // margin-right: 0;
        //     &>div {
        //         // margin-left: 8px;
        //     }
        // }
        .swtich-token {
            display: none;
        }
    }
}