@import '../../styles/share.scss';

.page-home{
  line-height: 1.5;
  overflow-x: hidden;

  h1, h2{
    font-size: 36px;
    font-weight: bold;

    strong{
      color: $primaryColor;
    }
  }

  input, button{
    &:focus{
      outline: 0;
    }
  }

  .button{
    text-transform: capitalize;
    display: inline-block;
    text-align: center;
    line-height: 78px;
    width: 180px;
    height: 78px;
    background: #181D23;
    color: #fff;
    border-radius: 5px;
    font-size: 18px;
    font-weight: bold;

    &:hover{
      background: lighten(#181D23, 10);
    }
  }

  .center-box{
    border: 1px solid #eee;
    border-radius: 20px;
    box-shadow: 4px 4px 6px rgba(0, 0, 0, .1);
    transform: translateY(50%);
    bottom: 0;
    position: absolute;
    background: #fff;
    box-sizing: border-box;
    width: 100%;
    color: #181D23;
  }

  .check-list{
    li{
      font-size: 16px;
      color: #181D23;
      margin-bottom: 36px;
      line-height: 1.5;
      display: flex;
      font-weight: bold;

      &:before{
        content: "";
        display: block;
        width: 18px;
        height: 18px;
        background: url("./images/icon_item.png") center center / contain no-repeat;
        margin-right: 12px;
        position: relative;
        top: 3px;
        flex-shrink: 0;
      }

      strong{
        color: $primaryColor;
      }
    }
  }

  .search{
    //max-width: 933px;
    display: flex;
    margin-top: 80px;

    input{
      flex-grow: 1;
      border: 2px solid #DEDEDE;
      border-radius: 5px 0 0 5px;
      border-right: 0;
      font-size: 18px;
      padding: 0 12px;

      &::-webkit-input-placeholder { /* WebKit browsers */
        color: #CFCFCF;
      }

      &:focus{
        border-color: $primaryColor;
      }
    }

    .button{
      border-radius: 0 5px 5px 0;
    }
  }

  .box1{
    width: 100%;
    background: #9488f0;
    padding-top: 80px;
    position: relative;

    h1{
      color: #181D23;

      strong{
        color: #fff;
      }
    }

    &:before, &:after{
      content: '';
      position: absolute;

      background-repeat: no-repeat;
      background-size: contain;
    }

    &:before{
      width: 387px;
      height: 355px;
      background-image: url("./images/box1_bg1.png");
      background-position: left top;
      top: 0;
      left: 0;
    }

    &:after{
      width: 673px;
      height: 612px;
      background-image: url("./images/box1_bg2.png");
      background-position: right bottom;
      //bottom: 73px;
      bottom: -13px;
      right: 0;
      top: auto;
    }

    &-inner{
      width: 80%;
      max-width: 955px;
      padding-top: 102px;
      padding-bottom: 210px;
      position: relative;
      color: #fff;
      z-index: 2;
    }

    .p1{
      color: #181D23;
      //color: #949494;
      font-size: 24px;
      margin-top: 16px;
    }

    .list{
      max-width: 933px;
      display: flex;
      flex-wrap: wrap;
      margin-top: 10px;

      li{
        font-size: 18px;
        color: #7578B5;
        width: 164px;
        margin-left: 12px;
        height: 44px;
        display: flex;

        i{
          display: block;
          width: 26px;
          height: 26px;
          background-size: contain;
          margin-right: 12px;
          background-position: center;
          background-repeat: no-repeat;
        }
      }
    }

    .center-box{
      padding: 60px 60px;
      text-align: center;
      font-size: 22px;
      font-weight: bold;
      display: flex;
      justify-content: center;

      p{
        position: relative;

        i{
          width: 70px;
          height: 50px;
          position: absolute;
          background: url("./images/quotation.png") center top / contain no-repeat;
          top: -10px;
          left: -16px;
        }

        span{
          position: relative;
          z-index: 2;
        }
      }
    }
  }

  .box2{
    //background-image: linear-gradient(#F8F8F8 60%, #FFF);
    padding-top: 240px;
    min-height: 600px;

    .check-list{
      margin-top: 50px;
    }

    .button{
      margin-top: 20px;
    }

    &-main{
      display: flex;
      flex-direction: row-reverse;

      .left{
        flex-shrink: 0;
        max-width: 595px;
        position: relative;
        z-index: 2;
      }

      .right{
        flex-grow: 1;
        position: relative;
        //flex-shrink: 1;
      }
    }
  }

  .box3{
    //height: 500px;
    text-align: center;
    padding-top: 160px;
    padding-bottom: 50px;

    h2{
      margin-top: 20px;
    }

    img{
      width: 80px;
    }

    .check-list{
      display: flex;
      justify-content: space-between;
      margin-top: 60px;
      padding-left: 10%;
      padding-right: 10%;
      gap: 20px;
    }

    .button{
      margin-top: 50px;
      background: $primaryColor;

      &:hover{
        background: lighten($primaryColor, 4);
      }
    }
  }

  .box4{
    min-height: 536px;
    position: relative;
    color: #fff;
    margin-top: 100px;

    &::before{
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      background: #181D23;
      transform: skewY(-8deg);
      z-index: 0;
    }

    .center{
      position: relative;
      z-index: 1;
      padding-top: 135px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;

      .left{

      }

      .right{
        width: 624px;
        height: 358px;
        background: #202C43;
        border-radius: 5px;
        top: 67px;
        position: relative;
        padding: 50px 18px;
        box-sizing: border-box;
        display: flex;

        .index{
          width: 30px;
          color: #434876;

          .row{
            display: block;
          }
        }

        .code{
          letter-spacing: 1px;
          white-space: pre-wrap;
          color: #eee;

          .var {
            color: $primaryColor;
          }

          .string{
            color: #e68217;
          }

          @keyframes typedjsBlink{
            50% { opacity: 0.0; }
          }

          &:after{
            content: "|";
            animation: typedjsBlink 0.7s infinite;
          }
        }

        .row{
          flex-grow: 1;
          //display: block;
          height: 28px;
          font-size: 18px;
          line-height: 28px;
        }
      }
    }

    .p1{
      font-size: 42px;
      font-weight: bold;
      font-family: sans-serif;
      color: $primaryColor;
      letter-spacing: 2px;
      margin-bottom: 20px;
    }

    .p2{
      font-size: 18px;
      margin-top: 25px;
    }

    .check-list {
      margin-top: 40px;

      li{
        color: #fff;
      }
    }
  }

  .box5{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 200px;

    img{
      height: auto;
    }

    .box{
      text-align: center;
      margin-left: 94px;

      strong{
        font-size: 58px;
        color: $primaryColor;
        margin-top: 10px;
        display: block;
        min-width: 400px;
        white-space: nowrap;
        text-align: center;
      }

      p{
        margin-top: 20px;
        font-size: 18px;
      }
    }
  }

  .box6{
    padding-top: 240px;
    //padding-bottom: 200px;
    text-align: center;
    position: relative;

    ul{
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      //gap: 148px;
      margin-top: 114px;
      gap: 20px;

      li{
        font-size: 20px;
        color: #9BA5BA;
        font-weight: bold;
      }
    }

    .center-box{
      width: 80%;
      max-width: 1014px;
      padding: 54px 30px;
      display: flex;
      text-align: left;
      color: #333;
      gap: 20px;
      margin-left: 50%;
      transform: translateX(-50%) translateY(50%);

      .p1{
        flex-grow: 1;

        h3{
          font-size: 22px;
        }

        p{
          font-size: 16px;
          font-weight: bold;
          margin-top: 15px;
        }
      }

      .input{
        flex-grow: 20;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 5px;
        border: 1px solid #EBEBEB;
        height: 76px;
        padding: 0 12px;

        input{
          flex-grow: 1;
          border: 0;
          height: 100%;
          font-size: 16px;
          font-weight: bold;
          background: none;

          &::-webkit-input-placeholder { /* WebKit browsers */
            color: #BCBCBC;
          }
        }
      }
    }
  }
}

.view1 .page-home{
  .box1{
    &:before{
      width: 300px;
    }

    &:after{
      width: 420px;
      bottom: -8px;
    }
  }
}

.view2 .page-home{
  h1, h2{
    font-size: 17px;
  }

  .center-box{
    border-radius: 10px;
  }

  .button{
    height: 50px;
    width: 140px;
    line-height: 50px;
    font-size: 14px;
    margin-left: auto;
    margin-right: auto;
  }

  .check-list{
    li{
      display: block;
      text-align: center;
      font-size: 12px;
      margin-bottom: 22px;

      &:before{
        display: none;
      }
    }
  }

  .box1{
    padding-top: 24px;
    padding-bottom: 0;

    &-inner{
      padding-top: 40px;
      padding-bottom: 60px;
    }

    &:before{
      width: 100px;
    }

    &:after{
      width: 130px;
      bottom: -2px;
    }

    .button{
      height: 50px;
      width: 100%;
      line-height: 50px;
      font-size: 14px;
    }

    //h1{
    //  max-width: 80%;
    //}

    .p1{ display: none;}

    .button2{
      background: $primaryColor;
      margin-top: 18px;

      &:hover{
        background: lighten($primaryColor, 4);
      }
    }

    .list{
      max-width: none;
      display: flex;
      justify-content: center;
      //gap: 10px;
      gap: 0;

      li{
        font-size: 12px;
        width: auto;
        margin-left: 0;
        min-width: 24%;
        max-width: 25%;
        height: 24px;
        margin-bottom: 3px;

        i{
          width: 15px;
          height: 15px;
          margin-right: 4px;
        }
      }
    }

    .center-box{
      //display: none;
      font-size: 12px;
      padding: 16px 20px;

      i{
        width: 30px;
        top: -5px;
        left: -10px;
      }
    }
  }

  .box1_2{
    margin: 80px 0 40px 0;
    .search{
      display: block;
      margin-top: 20px;

      input{
        height: 46px;
        width: 100%;
        box-sizing: border-box;
        border: 1px solid #dedede;
        border-radius: 5px;
        font-size: 14px;
        background: white;

        &::-webkit-input-placeholder {
          font-size: 11px;
          letter-spacing: 0;
        }

        &:focus{
          border-color: $primaryColor;
        }
      }
    }

    .button{
      border-radius: 5px;
      margin-top: 18px;
      width: 100%;

      &.button2{
        background: $primaryColor;
      }
    }
  }

  .box2{
    padding-top: 30px;
    background: none;

    .check-list{
      margin-top: 30px;
      width: 80%;
    }

    h2{
      text-align: center;
    }

    &-main{
      flex-direction: column-reverse;

      .left{
        display: flex;
        flex-direction: column;
        margin-top: 35px;
        align-items: center;
        max-width: none;
      }
    }
  }

  .box3{
    padding-top: 70px;
    padding-bottom: 0;

    img{
      width: 56px;
    }

    .check-list{
      gap: 10px;
      flex-direction: column;
      margin-top: 40px;
    }
  }

  .box4{
    &:before{
      height: calc(100% - 50px)
    }

    .center{
      display: block;
      padding-top: 10%;

      .p1{
        font-size: 22px;
        margin-bottom: 15px;
      }

      .p2{
        width: 90%;
        font-size: 14px;
        margin-top: 20px;
      }

      .right{
        width: 100%;
        top: 20px;
        height: auto;
        padding: 25px 15px;

        .row{
          font-size: 14px;
          height: 19px;
          line-height: 19px;
        }

        .code{
          line-height: 19px;
        }

        .index{
          width: 24px;
          .row{
            height: 20px;
            line-height: 20px;
          }
        }
      }
    }

    .check-list{
      margin-top: 30px;

      li{
        display: flex;
        &:before{
          display: block;
          width: 15px;
          height: 15px;
          margin-top: -1px;
          margin-right: 10px;
        }
      }
    }
  }

  .box5{
    padding-top: 100px;

    img{
      display: none;
    }

    .box{
      margin-left: 0;

      strong{
        width: auto;
        font-size: 32px;
        margin-top: 15px;
      }

      p{
        font-size: 14px;
      }
    }
  }

  .box6{
    padding-top: 40px;
    padding-bottom: 40px;

    ul{
      margin-top: 40px;
      flex-wrap: wrap;
      gap: 0;
      justify-content: space-evenly;

      li{
        width: 50%;
        margin-bottom: 40px;
        transform: scale(0.8);
      }
    }

    .center-box{
      margin-top: 60px;
      padding: 0;
      display: block;
      text-align: left;
      color: #333;
      width: 100%;
      border: 0;
      box-shadow: none;
      transform: none;
      position: relative;
      margin-left: 0;

      .p1{
        h3{
          font-size: 16px;
        }
        p{
          font-size: 12px;
          margin-top: 5px;
        }

      }

      .input{
        height: 46px;
        padding: 0 10px;
        margin-top: 15px;

        input{
          font-size: 12px;
        }

        img{
          width: 26px;
        }
      }
    }
  }
}
