.c-home-footer{
  background: #181D23;
  padding-top: 230px;
  padding-bottom: 160px;

  &-main{
    color: #fff;
    display: flex;
    gap: 60px;
    justify-content: space-between;


    .left{
      //flex-grow: 1;
      padding-right: 10%;
      border-right: 2px solid #3A3E44;
      padding-bottom: 10px;

      img{
        display: block;
      }

      .language-wrapper{
        position: relative;
      }

      .language{
        width: 186px;
        display: flex;
        flex-direction: row;
        padding: 20px 17px;
        border: 1px solid #5F5F61;
        margin-top: 60px;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        margin-bottom: 50px;

        img{
          &:first-of-type{
            margin-right: 12px;
          }
        }

        span{
          flex-grow: 1;
        }
      }

      .dropdown{
        background: #181D23;
        position: absolute;
        width: 220px;
        box-sizing: content-box;
        top: 67px;
        z-index: 10;
        border: 1px solid #5F5F61;
        left: 0;
        padding: 10px 0;

        .item{
          font-size: 22px;
          line-height: 1.5;
          color: #888;
          padding: 6px 25px;
          cursor: pointer;
          text-align: center;

          &.active{
            color: #fff;
            cursor: default;
          }
        }
      }
    }

    .right{
      flex-grow: 1;
      flex-shrink: 0;
      display: flex;
      justify-content: space-between;
      max-width: 840px;
    }

    dt{
      font-size: 16px;
      color: #DEE4E4;
      font-weight: bold;
      margin-bottom: 20px;
    }

    dd a{
      display: flex;
      align-items: center;
      margin-bottom: 18px;
      color: #E4E4E4;

      img{
        margin-right: 10px;
      }
    }
  }
}

.view2 .c-home-footer{
  padding-top: 40px;
  padding-bottom: 10px;

  .c-home-footer-main{
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 0;

    & > div, dl{
      min-width: 40%;
      margin-bottom: 30px;
    }

    dt{
      font-size: 14px;
    }

    dd a{
      font-size: 12px;
      margin-bottom: 12px;

      img{
        width: 20px;
        margin-right: 6px;
      }
    }
  }

  .left{
    padding-right: 0;
    border: 0;

    .logo{
      width: 140px;
    }

    .language{
      width: 120px;
      margin-top: 30px;
      padding: 7px 6px;
      font-size: 12px;
      margin-bottom: 30px;

      img{
        &:first-of-type{
          width: 18px;
          margin-right: 6px;
        }

        &:last-of-type{
          width: 10px;
        }
      }
    }

    .dropdown{
      top: 33px;
      width: 118px;

      .item{
        font-size: 14px;
      }
    }

    p{
      font-size: 10px;
    }
  }

}
