@import '../../../styles/share.scss';
.sidebar {
    background-color: #1e242c;
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
    .switch {
        position: relative;
        height: 36px;
        padding: 6px 16px;
        @include flex();
        button {
            flex: 1;
            height: 100%;
            color: #fff;
            text-align: center;
            font-weight: bold;
            font-size: 16px;
            z-index: 2;
            cursor: pointer;
        }
        &::after {
            content: '';
            display: inline-block;
            width: 50%;
            height: 100%;
            background-color: #4f5a69;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate(2%, -50%);
            transition: transform linear 100ms;
            will-change: transform;
            z-index: 1;
        }
        &.liqiudity {
            &::after {
                transform: translate(0, -50%);
            }
        }
        &.trade {
            &::after {
                transform: translate(100%, -50%);
            }
        }
    }
    .pool-activity {
        //padding-top: 12px;
        .pool {
            @include flex(space-between, stretch);
            height: 36px;
            padding: 0 8px;
            .title {
                color: #fff;
                font-size: 18px;
                font-weight: 500;
                line-height: 36px;
            }
            .filter {
                @include flex(space-around);
                font-size: 14px;
                button {
                    background-color: steelblue;
                    border-radius: 3px;
                    padding: 4px 8px;
                    text-align: center;
                    font-size: 14px;
                    color: #fff;
                    &+button {
                        margin-left: 8px;
                    }
                    &:hover,
                    &.active {
                        background-color: #9488f0;
                    }
                }
            }
        }
    }
}