.c-footer{
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  margin: 16px 0;

  p{
    height: 30px;
    line-height: 30px;
    opacity: 0.5;
  }

  a{
    height: 30px;
    line-height: 30px;
    display: inline-block;
    color: #9488f0 !important;
    margin-left: 10px;

    &:first-of-type{
      margin-left: 0;
    }
  }
}

.view2{
  .c-footer{
    padding-left: 15px;
    padding-right: 15px;
    display: block;
  }
}
