.setting-modal {
    font-weight: 500;
    .title {
        text-align: center;
        color: #fff;
        font-size: 20px;
    }
    .setting-cont {
        padding-top: 16px;
        .cont-item {
            display: flex;
            justify-content: space-between;
            margin-bottom: 24px;
            .label {
                max-width: 60%;
                .subtitle {
                    font-size: 16px;
                    color: #fff;
                }
                p {
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 18px;
                    color: #6d7986;
                    margin: 12px 0;
                }
            }
            .action {
                button {
                    position: relative;
                    display: inline-block;
                    width: 58px;
                    height: 32px;
                    background-color: #181d23;
                    border-radius: 29px;
                    -webkit-tap-highlight-color: transparent;
                    &::after {
                        position: absolute;
                        content: "";
                        height: 26px;
                        width: 26px;
                        left: 4px;
                        bottom: 4px;
                        background-color: #6d7986;
                        transition: .4s;
                        border-radius: 50%;
                    }
                    &.on {
                        &::after {
                            transform: translateX(26px);
                            background-color: #22d59f;
                        }
                    }
                }
                .percent {
                    position: relative;
                    align-items: center;
                    display: flex;
                    width: 54px;
                    height: 30px;
                    box-sizing: border-box;
                    input {
                        width: 54px;
                        height: 30px;
                        background-color: #1f262f;
                        color: #fff;
                        border-radius: 3px;
                        text-align: center;
                        padding: 8px 8px 6px 16px;
                        position: relative;
                        z-index: 1;
                        box-sizing: border-box;
                        border: none;
                        outline: 0;
                    }
                    &::before {
                        content: "";
                        position: absolute;
                        top: -1px;
                        bottom: -1px;
                        left: -1px;
                        right: -1px;
                        background: linear-gradient( 77.45deg, #9488f0 30%, #e967c4 70%, #82cf8a 110%);
                        border-radius: 3px;
                        z-index: 0;
                    }
                }
            }
            .links {
                display: flex;
                align-items: center;
                // margin-top: 20px;
                a {
                    padding: 8px 10px;
                    background-color: #343d48;
                    border-radius: 3px;
                    color: #6d7986;
                    text-decoration: none;
                    font-style: normal;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 21px;
                    margin: 12px 8px 0 0;
                    display: flex;
                    align-items: center;
                }
            }
            &.language {
                .links {
                    a {
                        margin: 0 0 0 8px;
                        cursor: pointer;
                        &.active {
                            color: #fff !important;
                            cursor: default;
                            background: #22d59f !important;
                        }
                        &:hover {
                            color: #ddd;
                            background: #3f4b5c;
                        }
                    }
                }
            }
        }
    }
}

.view2 {
    * {
        box-sizing: border-box;
    }
    .setting-modal {
        padding-top: 16px;
    }
    .setting-cont {
        padding-left: 16px;
        padding-right: 16px;
    }
}

@media screen and (max-width:375px) {
    .language {
        flex-direction: column;
        .label {
            margin-bottom: 12px;
        }
    }
}