@import '../../../../styles/share.scss';

.c-home-header{
  height: auto;

  &.zeroHeight{
    height: 0;
  }

  &-inner{
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 10;
  }

  .h1{
    display: flex;
    align-items: center;
  }

  .logo{
    display: block;
    width: 218px;
    margin-right: 15px;
  }

  .nav{
    display: flex;
    gap: 48px;
    margin-top: -15px;

    .item, .item a{
      //color: #181D23;
      color: #fff;
      font-size: 16px;
      display: flex;
      align-items: center;
      cursor: pointer;
      text-transform: capitalize;

      img{
        margin-left: 8px;
      }
    }

    button{
      background: #fff;
      height: 45px;
      border-radius: 6px;
      color: $primaryColor;
      font-size: 17px;
      padding: 0 20px;
    }
  }
}

.view2 .c-home-header{
  .c-home-header-inner{
    height: 50px;
  }

  .logo{
    width: 120px;
    margin-right: 10px;
  }

  .menu{
    &-btn{
      width: 18px;
      height: 18px;
      background: url('./images/menu.png') center center / contain no-repeat;
    }

    &.active{
      .menu-btn{
        background-image: url('./images/cross.png');
      }
    }

    .c-dropdown-content{
      width: 100%;
      background: #181D23;
      right: 0;
      top: 50px;
      border-radius: 3px ;
      display: flex;
      flex-wrap: wrap;
      padding: 40px 30px;

      .list{
        a{
          display: flex;
          font-size: 18px;
          color: #ccc;
          text-transform: capitalize;
          height: 40px;
          line-height: 40px;
          border-bottom: 1px solid rgba(255, 255, 255, .3);
          margin-top: 20px;
          justify-content: space-between;
          align-items: center;

          &:first-of-type{
            margin-top: 0;
          }

          &:after{
            content: '';
            width: 8px;
            height: 16px;
            background: url('./images/arrow-right.png') center center / contain no-repeat;
          }

        }
      }

