@import '../../../../styles/share.scss';
.c-trade-header {
    padding-top: 5px;
    .title {
        padding: 0 8px;
        color: #fff;
        font-size: 18px;
        font-weight: 500;
        line-height: 36px;
    }
    .amount {
        margin: 4px 8px 0 8px;
        display: flex;
        p {
            margin: 0;
            &:nth-of-type(1) {
                color: #fff;
                font-size: 32px;
                //font-weight: bold;
                line-height: 1;
                i {
                    font-style: normal;
                    font-weight: normal;
                    opacity: .4;
                }
            }
            &:nth-of-type(2) {
                color: $upColor;
                margin-left: 8px;
            }
        }
    }
    .tab-btn {
        display: flex;
        margin-top: 10px;
        justify-content: flex-end;
        button {
            border-radius: 3px;
            padding: 4px 8px;
            text-align: center;
            font-size: 14px;
            color: rgba(255, 255, 255, 0.75);
            margin-right: 4px;
            &:hover {
                color: #fff;
            }
            &.active {
                background-color: #9488f0;
                color: #fff;
            }
        }
    }
}

@media screen and (max-width:$mobile) {
    .c-trade-header {
        .title {
            display: none;
        }
    }
}