@import '../../../../styles/share.scss';
.dyor-modal {
    .wallet {
        display: flex;
        padding: 8px 16px;
        color: #fff;
        .unittoken {
            display: flex;
            align-items: center;
            margin-left: 6px;
            span {
                margin: 0 8px;
                color: $upColor;
            }
        }
        .copy-info {
            padding: 0 8px;
            display: flex;
            align-items: center;
            span {
                margin: 0 8px;
            }
            button {
                margin-right: 6px;
            }
        }
    }
    .panel {
        margin-top: 20px;
        margin-bottom: 24px;
        padding: 24px 24px 32px;
        border-radius: 3px;
        background-color: #2c343e;
        color: #fff;
        .float-price {
            font-weight: 400;
            font-size: 24px;
            line-height: 36px
        }
        .sign {
            display: flex;
            align-items: center;
            font-size: 13px;
            margin: 6px 0;
            &.up {
                color: $upColor;
            }
            &.down {
                color: $downColor;
            }
        }
        .list {
            display: flex;
            flex-wrap: wrap;
            &>div {
                width: 50%;
                margin-bottom: 24px;
                &:nth-of-type(3),
                &:nth-of-type(4) {
                    margin-bottom: 0;
                }
                span {
                    display: inline-block;
                    width: 100%;
                }
                .title {
                    font-size: 13px;
                    line-height: 18px;
                    color: #6d7986;
                    margin-bottom: 4px
                }
                .sum {
                    font-size: 18px;
                    line-height: 27px;
                    padding: 8px 0 2px;
                }
            }
        }
    }
    .colum {
        width: 100%;
        display: flex;
        align-items: center;
        margin-bottom: 16px;
        span {
            display: inline-block;
            font-size: 13px;
            &:nth-of-type(1) {
                line-height: 18px;
                color: #6d7986;
                width: 100px;
            }
            &.rank {
                line-height: 18px;
                margin-left: 16px;
                padding: 7px 12px;
                background-color: #181d23;
                border-radius: 8px;
                color: #fff;
            }
        }
        .io {
            margin-left: 16px;
            background-color: #29313a;
            border-radius: 8px;
            color: #fff;
            opacity: .4;
            font-size: 12px;
            line-height: 18px;
            padding: 7px 12px
        }
        &>img {
            margin-left: 18px;
        }
    }
}

.view2 {
    .dyor-modal {
        box-sizing: border-box;
        padding-bottom: 24px;
        padding-top: 16px;
        .panel {
            box-sizing: border-box;
            width: 95%;
            border-radius: 10px;
            margin: 16px auto;
            padding: 16px 16px 24px;
            .list {
                &>div {
                    margin-bottom: 16px;
                    &:nth-of-type(2n+2) {
                        .title,
                        .sum {
                            text-align: right;
                        }
                    }
                    &:nth-of-type(3),
                    &:nth-of-type(4) {
                        margin-bottom: 0;
                    }
                    .sum {
                        word-wrap: break-word;
                        break-inside: avoid-column;
                    }
                }
            }
        }
        .colum {
            width: 90%;
            padding-bottom: 10px;
            margin: 0 auto;
        }
    }
}