@import '../../../../styles/share.scss';
.dats-logo {
    max-width: 300px;
    min-width: 368px;
    // width: 368px;
    ;
    a {
        @include flex();
        padding-left: 24px;
        justify-content: flex-start;
        img {
            display: inline-block;
            width: 36px;
            height: 36px;
            border: none;
        }
        .dats-name {
            font-size: 28px;
            line-height: 1.2;
            color: #9488f0;
            margin-left: 30px;
        }
    }
}

@media screen and (max-width: $mobile) {
    .dats-logo {
        display: none;
    }
}