@import '../../../styles/share.scss';
.p-mobile-token {
    background: #1e242c;
    padding-bottom: 56px;
    .chart-wrapper {
        //transition: all ease 120ms;
        overflow: hidden;
        height: 300px;
        background: #232a32;
    }
    .switch {
        position: relative;
        height: 40px;
        padding-bottom: 2px;
        @include flex();
        button {
            flex-grow: 1;
            width: 50%;
            height: 100%;
            color: #fff;
            text-align: center;
            font-weight: bold;
            font-size: 16px;
            z-index: 2;
            cursor: pointer;
            -webkit-tap-highlight-color: transparent;
            &.tabswap {
                display: flex;
                justify-content: center;
                align-items: center;
                img {
                    margin-left: 10px;
                }
            }
            &.tabliquidity {
                i {
                    display: inline-block;
                    width: 0;
                    height: 0;
                    border-width: 4px;
                    border-color: transparent #fff #fff transparent;
                    border-style: solid;
                    margin-left: 6px;
                    position: relative;
                    z-index: 10;
                }
                .tips-buttons {
                    min-width: 30px;
                    overflow: hidden;
                    a {
                        display: block;
                        padding: 6px 0;
                        font-size: 14px;
                    }
                }
            }
        }
        &::after {
            content: '';
            display: inline-block;
            width: 33.333333333%;
            height: 2px;
            background-color: #9488f0;
            position: absolute;
            left: 0;
            bottom: 0;
            transform: translate(0, 0);
            transition: transform linear 100ms;
            will-change: transform;
            z-index: 1;
        }
        &.swap {
            &::after {
                transform: translate(0, 0);
            }
        }
        &.liquidity {
            &::after {
                transform: translate(100%, 0);
            }
        }
        &.trade {
            &::after {
                transform: translate(200%, 0);
            }
        }
    }
}

.trade-panel {
    flex-grow: 1;
    flex-shrink: 1;
    //background: #1f242b;
    padding: 20px 10px 26px 10px;
    box-sizing: border-box;
    h2 {
        font-weight: normal;
        font-size: 22px;
        text-align: center;
    }
    .trade-button {
        height: 48px;
        width: 100%;
        background: $upColor;
        margin-top: 24px;
        color: #fff;
        font-size: 18px;
        border-radius: 3px;
        &.sell {
            background: $downColor;
        }
    }
}
