$upColor:#46d7ab;
$downColor:#ff646d;
$mobile:960px;
$primaryColor:#9089e9;
@mixin flex($justify:space-between, $align:center) {
    display: flex;
    justify-content: $justify;
    align-items: $align;
}

@mixin controlbg {
    padding: 8px;
    background-color: #232a32;
    border-radius: 3px;
    cursor: pointer;
}
