.view2{
  .kline-chart{
    margin-right: 0;
  }
  .kline-wrapper{
    padding: 0;

    .kline-center{
      min-height: 290px;
    }
  }

}

.kline-wrapper{
  width: 100%;
  //resize: vertical;
  //overflow: scroll;
  display: flex;
  flex: 1 1;
  //flex-shrink: 1;
  //flex-grow: 0;
  flex-direction: column;
  z-index: 10;
  background-color: #232a32;
  //justify-self: flex-end;
  padding-top: 8px;
  padding-bottom: 15px;

  button{
    display: block;
    border-radius: 3px;
    //width: 50px;
    //height: 50px;
    background: #000;
    border: 0;
    cursor: pointer;

    &:focus{
      outline: 0;
    }
  }

  //&.fullscreen{
  //  position: fixed;
  //  top: 0;
  //  left: 0;
  //  z-index: 10000;
  //  width: 100vw;
  //  height: 100vh;
  //  min-height: auto;
  //  resize: none;
  //}

  &.fullscreen{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    width: 100vw !important;
    height: 100vh !important;
    min-height: auto;
    resize: none;

    .kline-center{
      min-height: auto;
    }

    //&.landscape{
    //  width: 100vh !important;
    //  height: 100vw !important;
    //  transform: rotate(90deg) translateY(-100vw) !important;
    //  transform-origin: top left;
    //}
  }

  .resize{
    min-width: 20px;
    min-height: 20px;
    //resize:both;

    //background: red;

  }

  .exitFullScreen{
    //width: 70px;
    padding: 0 12px;
    height: 28px;
    background: #e64852;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 100001;
    color: #fff;
    border: 0;
    border-radius: 0 0 0 3px;
  }

  .kline-center{
    display: flex;
    width: 100%;
    min-height: 515px;
    flex-grow: 1;
    flex-shrink: 1;
    resize: vertical;
    overflow: hidden;
  }
}

.kline-sidebar{
  width: 40px;
  margin-left: 10px;

  ul{
    li{
      cursor: pointer;
      //border: 5px solid transparent;
      width: 22px;
      height: 22px;
      border-radius: 3px;
      //margin-top: 4px;
      border: 3px solid transparent;

      &:hover{
        background: #191d22;
        color: #8e9093;
      }

      img{
        width: 100%;
        height: 100%;
      }
    }
  }

  .div{
    width: 28px;
    height: 1px;
    background-color: #ffffff33;
    margin: 10px 0;
  }

}

.kline-topBar{
  width: 100%;
  height: 34px;
  flex-shrink: 0;
  flex-grow: 0;
  margin-left: 10px;

  ul{
    display: flex;

    li{
      cursor: pointer;
      margin-right: 5px;
      height: 24px;
      padding: 0 5px;
      font-size: 13px;
      border-radius: 3px;
      line-height: 24px;

      &:hover{
        background: #191d22;
      }

    }
  }
}

.kline-bottomBar{
  width: 100%;
  height: 50px;
  background: blue;
  flex-shrink: 0;
  flex-grow: 0;
}

.kline-chart{
  display: flex;
  flex-grow: 1;
  flex-shrink: 1;
  margin-right: 10px;
}
