 .g-loader {
     background: #000;
     background: radial-gradient(#222, #000);
     bottom: 0;
     left: 0;
     overflow: hidden;
     position: fixed;
     right: 0;
     top: 0;
     z-index: 99999;
     .loader-inner {
         position: absolute;
         top: 30%;
         left: 50%;
         transform: translateX(-50%);
         width: 100px;
         height: 60px;
         .loader-line-wrap {
             animation: spin 2000ms cubic-bezier(.175, .885, .32, 1.275) infinite;
             box-sizing: border-box;
             height: 50px;
             left: 0;
             overflow: hidden;
             position: absolute;
             top: 0;
             transform-origin: 50% 100%;
             width: 100px;
             &:nth-child(1) {
                 animation-delay: -50ms;
                 .loader-line {
                     border-color: hsl(0, 80%, 60%);
                     height: 90px;
                     width: 90px;
                     top: 7px;
                 }
             }
             &:nth-child(2) {
                 animation-delay: -100ms;
                 .loader-line {
                     border-color: hsl(60, 80%, 60%);
                     height: 76px;
                     width: 76px;
                     top: 14px;
                 }
             }
             &:nth-child(3) {
                 animation-delay: -150ms;
                 .loader-line {
                     border-color: hsl(120, 80%, 60%);
                     height: 62px;
                     width: 62px;
                     top: 21px;
                 }
             }
             &:nth-child(4) {
                 animation-delay: -200ms;
                 .loader-line {
                     border-color: hsl(180, 80%, 60%);
                     height: 48px;
                     width: 48px;
                     top: 28px;
                 }
             }
             &:nth-child(5) {
                 animation-delay: -250ms;
                 .loader-line {
                     border-color: hsl(240, 80%, 60%);
                     height: 34px;
                     width: 34px;
                     top: 35px;
                 }
             }
             .loader-line {
                 border: 4px solid transparent;
                 border-radius: 100%;
                 box-sizing: border-box;
                 height: 100px;
                 left: 0;
                 margin: 0 auto;
                 position: absolute;
                 right: 0;
                 top: 0;
                 width: 100px;
             }
         }
     }
 }
 
 @keyframes spin {
     0%,
     15% {
         transform: rotate(0);
     }
     100% {
         transform: rotate(360deg);
     }
 }