@import "../../../../styles/share.scss";
.wallet-container {
    display: flex;
    align-items: center;
    padding-right: 24px;
    border-radius: 3px;
    &>button {
        margin: 0 10px;
        font-weight: 500;
    }
    .wallet {
        min-width: 80px;
        margin: 0 16px 0 12px;
        @include flex();
        @include controlbg();
        .addr {
            font-size: 14px;
            padding: 0 12px;
            line-height: 1;
            color: #fff;
        }
        &.Unconnect,
        &.Errormsg {
            padding-left: 12px;
            padding-right: 12px;
            justify-content: space-around;
            button {
                display: block;
                height: 100%;
                font-size: 16px;
            }
        }
        &.Errormsg {
            button {
                color: $downColor;
            }
        }
        &.Unconnect {
            button {
                color: #9488f0;
            }
        }
    }
}

@media screen and (max-width:$mobile) {
    .wallet-container {
        display: none;
    }
}